.box {
  margin: 25px 0 -10px 0;
  width: 980px;
  height: 120px;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.box .title {
  width: 100%;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  height: inherit;
}

.box .title .block {
  width: 0%;
  height: inherit;
  background: #77abb7;
  position: absolute;
  -webkit-animation: mainBlock 2s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
          animation: mainBlock 2s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
  display: -webkit-flex;
  display: flex;
}

.box .title h2 {
  color: #fff;
  font-size: 150px;
  -webkit-animation: mainFadeIn 2s forwards;
  animation: mainFadeIn 2s forwards;
  -webkit-animation-delay: 1.6s;
          animation-delay: 1.6s;
  opacity: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: baseline;
          align-items: baseline;
  position: relative;
}

.box .title h2 span {
  width: 0px;
  height: 0px;
  border-radius: 50%;

  background: #77abb7;
  -webkit-animation: load 0.6s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
  animation: popIn 0.8s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  margin-left: 5px;
  margin-top: -10px;
  position: absolute;
  bottom: 33px;
  right: -30px;
}

@-webkit-keyframes mainBlock {
  0% {
    width: 0%;
    left: 0;
  }
  50% {
    width: 100%;
    left: 0;
  }
  100% {
    width: 0;
    left: 100%;
  }
}

@keyframes mainBlock {
  0% {
    width: 0%;
    left: 0;
  }
  50% {
    width: 100%;
    left: 0;
  }
  100% {
    width: 0;
    left: 100%;
  }
}

@-webkit-keyframes mainFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes mainFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes popIn {
  0% {
    width: 0px;
    height: 0px;

    background: #77abb7;
    border: 0px solid #ddd;
    opacity: 0;
  }
  50% {
    width: 21px;
    height: 21px;

    background: #77abb7;
    opacity: 1;
    bottom: 92px;
  }
  65% {
    width: 21px;
    height: 21px;
    bottom: 47px;
  }
  80% {
    width: 21px;
    height: 21px;
    bottom: 67px;
  }
  100% {
    width: 21px;
    height: 21px;
    background: #77abb7;
    border: 0px solid #222;
    bottom: 60px;
  }
}

@keyframes popIn {
  0% {
    width: 0px;
    height: 0px;

    background: #77abb7;
    border: 0px solid #ddd;
    opacity: 0;
  }
  50% {
    width: 21px;
    height: 21px;

    background: #77abb7;
    opacity: 1;
    bottom: 92px;
  }
  65% {
    width: 21px;
    height: 21px;
    bottom: 47px;
  }
  80% {
    width: 21px;
    height: 21px;
    bottom: 67px;
  }
  100% {
    width: 21px;
    height: 21px;
    background: #77abb7;
    border: 0px solid #222;
    bottom: 60px;
  }
}

